@media (max-width: 1200px){

    .containerHomePageThree{
        min-height: 100vh;
        min-width: 100vw;
        background: rgb(0,0,0);
        //background: linear-gradient(90deg, rgba(0,0,0,1) 0%, rgba(31,31,31,1)  100%);
        background-image: url('../../img/home/pagethree/fondo.png');
        background-size: 45vw;
        background-repeat: no-repeat;
        background-position: center;
        display: flex;
        justify-content: center;
        align-items: center;
        .texto1,.texto2, .texto3{
            line-height: 2.8vh;
            color: #FFF;
        }
    
        .texto1{
            position: absolute;        
            min-width: 20vw;
            max-width: 20vw;
            margin-bottom: 25vh;
            margin-right: 65vw;
            font-size: 1.2vw;
            text-align: right;
            line-height: 2.8vh;
            font-family: ApexBook;
            color: rgb(216,216,216);
        }
    
    
        .texto2{
            position: absolute;        
            min-width: 20vw;
            max-width: 20vw;
            margin-bottom: -25vh;
            margin-right: 65vw;
            font-size: 1.2vw;
            text-align: right;
            line-height: 2.8vh;
            font-family: ApexBook;
            color: rgb(216,216,216);
        }
    
        .texto3{
            position: absolute;        
            min-width: 20vw;
            max-width: 20vw;
            margin-bottom: 0vh;
            margin-right: -65vw;
            font-size: 1.2vw;
            text-align: left;
            line-height: 2.5vh;
            font-family: ApexBook;
            color: rgb(216,216,216);
        }
    }
}