@media (max-width: 480px) {
    .containerTerritorios{
        min-height: 100vh;
        min-width: 100vw;
        display: flex;
        flex-direction: row;
    




        .select{
            display: flex;
            position: absolute;
            min-width: 100vw;
            display: flex;
            justify-content: center;
            align-items: center;
            min-height: 10vh;
            select{
                min-width: 80%;
                min-height: 5vh;
                border-radius: 1.5vw;
                font-size: 5vw;
                font-family: ApexBook;
                option{
                    font-size: 5vw;
                    min-width: 80vw;
                    border: none;
                    font-family: ApexBook;
                }
            }
        }


   



        


        section{
            min-width: 50vw;
    
    
    
            .fondo0, .fondo1,.fondo2,.fondo3,.fondo4,.fondo5,.fondo6,.fondo7,.fondo8,.fondo9,.fondo10,.fondo11,.fondo12{
                margin-top: 15vh;
                margin-left: 0vw;
                min-width: 80vw;
                max-height: 70vh;           
                
                background-size: cover;
                background-position: center;
            }
    
            .fondo0{    background-image: url('../../img/territories/img1.png');    }
            .fondo1{    background-image: url('../../img/territories/img2.png');    }
            .fondo2{    background-image: url('../../img/territories/img3.png');    }
