                   min-height: 80%;
                   text-align: center;
                   line-height: 20vw;
                   background-color: white;
                   writing-mode: vertical-lr;
                   transform: rotate(180deg);
                   background: linear-gradient(90deg, white , transparent 80%);
               }
    




                .cero{
                    font-size: 28vw;
