



.containerContentPageFive{
    background-color: transparent;
    min-width: 100vw;
    min-width: 100vw;
    min-height: 35vh;
    display: flex;
    flex-direction: column;
    align-items: center;

}























$animationSpeedFive: 30s;

// Animation
@keyframes scrollFive {
	0% { transform: translateX(0); }
	100% { transform: translateX(calc(-25vw * 9))}
}


// Styling
.sliderFive {
	background-color: none;
	min-width: 100vw;
	max-width: 100vw;
	
	min-height: 35vh;
	max-height: 35vh;
	overflow:hidden;
	overflow-x: hidden;
	display: flex;
	justify-content: flex-start;
	align-items: flex-end;
	&::before,
	&::after {
		content: "";
		height: 100%;
		width: 10px;
		z-index: 2;
	}
	
	&::after {
		right: 0;
		top: 0;
		transform: rotateZ(180deg);
	}

	&::before {
		left: 0;
		top: 0;
	}
	
	.slide-trackFive {
		animation: scrollFive $animationSpeedFive linear infinite;
		display: flex;
		width: calc(25vw * 18);
	}
	.slide-trackFive:hover {
		//animation-play-state: paused;
	}
	
	.slideFive {
		min-height: 30vh;
		min-width: 25vw;
		display: flex;
