@media (max-width: 850px) and (orientation:portrait){
        .containerHomePageone{
            background-color: #151515;
            min-height: 100vh;
            min-width: 100vw;
        
            display: flex;
            flex-direction: row;
            .sectionLeftHomePageOne{
                z-index: 1;
                padding-top: 2vh;
                min-width: 65vw;
                background-image: url('../../img/home/pageone/fondo.png');
                background-size: cover;
                background-position: center;
                display: flex;
                flex-direction: column;
                justify-content: flex-start;
                align-items: center;
                img{
                    margin-top: 3vh;
                    max-width: 80%;
                }
                hr{
                    position: absolute;
                    margin-top: 23vh;
                    border: 1.5px solid #C7974B;
                    background-color: #C7974B;
                    min-width: 20vw;
                }
                text{
                    display: none;              
                }
                .Logo{
                    max-width: 30%;
                    img{
                        max-width: 30%;
                    }
                }
            }
            .sectionRighttHomePageOne{
                z-index: 2;
                min-width: 35vw;
                display: flex;
                flex-direction: column;
                .text{
                    min-height: 60vh;
                    color: #FFF;
                    display: flex;
                    align-items: flex-end;
                    margin-left: 0vw;
                    label{
                        min-width: 80vw;
                        font-size: 8vw;
                        text-align: right;
                        margin-left: -55vw;
                        font-family: PpeikoMedium;
                    }
                }
                .sello{
                    min-height: 30vh;
                    display: flex;
                    align-items: center;
                    margin-top: 10vh;
                    margin-left: -15vw;
                    div{
                        background: rgb(224,190,141);
                        background: linear-gradient(90deg, rgba(224,190,141,1) 0%, rgba(199,151,75,1) 100%);
                        min-width: 30vw;
                        max-width: 30vw;
                        min-height: 18px;
                        max-height: 18px;                
                    }
                }
                
            }
        }
    
    
}