@media (max-width: 1185px) and (orientation:landscape){
    .containerGeneralFooter{
        min-width: 100vw;
        min-height: 25vh;
        margin-bottom: 0vh;
        padding-bottom: 0;
        background-image: url('../../img/footer/fondo.png');
        background-repeat: no-repeat;
        background-size: 100%;
        .sectionOneFooter{
            min-height: 15vh;   
            display: flex;
            justify-content: center;
            align-items: center;
            border-bottom: 1px solid #C7974B;
        }
        .sectionTwoFooter{
            min-height: 10vh;   
    
            ul{
                min-height: 10vh;
                list-style: none;            
                min-width: 100vw;
                display: flex;
                flex-direction: row;
                justify-content: center;
                align-items: center;
                display: none;
                li{
                    min-width: 10vw;
                    color: #E0BE8D;
                    font-size: 1.2rem;
                    display: flex;
                    justify-content: center;
                    .link{
                        list-style: none;
                        text-decoration: none;
                        color: #E0BE8D;
                        font-family: PublicoHeadlineLightItalic;
    
                        cursor: pointer;
    
    
                        background-repeat: no-repeat;
                        background-image:   linear-gradient(to right, #C9C9C9 100%, #C9C9C9 100%);
                        background-size:    20% 5%;//,
                        background-position:   0% 95%;      
    
    
                    }
    
                    .link:hover{
                        transition-duration: 0.5s;
                        background-size:    100% 5%;//,  
                    }
                }
            }
            .navimages{
                display: flex;
            }
        }
    }
}