.containerAboutPageThree{
    min-width: 100vw;
    min-height: 80vh;
    max-width: 100vw;
    max-height: 80vh;
    display: flex;
    align-items: flex-end;
    flex-direction: row;
    .sectionOneAboutPageThree{
        z-index: 3;
        min-width: 50vw;
        min-height: 100vh;
        .sectionContentOneAboutPageThree{
            min-height: 60vh;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            .one{
                position: absolute;
                min-width: 8vw;
                margin-bottom: 45vh;
                margin-right: 50vw;
                border: 1px solid  #707070;
            }
            .two{
                position: absolute;
                min-width: 3vw;
                max-width: 3vw;
                margin-right: 148vw;
                margin-top: 40vh;
                border: 1px solid  #707070;
            }
            h2{
                z-index: 5;
                position: absolute;
                font-family: PpeikoLight;
                font-size: 4vw;                
                font-weight: 400;
                color: #E0BE8D;
                min-width: 30vw;
                margin-top: 10vh;
                margin-right: -35vw;
            }
            h1{
                min-width: 30vw;
                z-index: 4;
                position: absolute;
                font-family: PpeikoMedium;
                font-size: 10vw;
                font-weight: 400;
                margin-left: 5vw;
                margin-top: 30vh;
            }
            .sello1{
                display: none;
                position: absolute;
                min-width: 8vw;
                min-height: 1.5rem;                
                background: rgb(224,190,141);
                background: linear-gradient(90deg, rgba(224,190,141,1) 0%, rgba(199,151,75,1) 100%);               
                margin-bottom: 10vh;
                margin-right: 0vw;
                z-index: 20;
            }
            .sello2{
                position: absolute;
                min-width: 20vw;
                max-width: 20vw;
                margin-right: -10vw;
                margin-top: 60vh;
                min-height: 1.5rem; 
                font-size: 1.5vw;
                color: #E0BE8D;            
                font-family: PpeikoLight;
            }
        }
    }
    .sectionTwoAboutPageThree{
        z-index: 5;
        min-width: 45vw;
        min-height: 100vh;
        display: flex;
        justify-content: flex-start;
        align-items: flex-start;

        .text{
            margin-top: 45vh;
            border-top: 1px solid #E0BE8D;
            min-width: 80%;
            display: flex;
            flex-direction: row;
            ul{
                min-width: 15vw;                            
                max-width: 15vw;
                text-decoration: none;
                list-style: none;
                border-right: 1px solid #E0BE8D;
                font-family: PpeikoThin;
                li{
                    cursor: pointer;
                    min-height: 8vh;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    font-size: 4vh;
                    min-width: 80%;
                    font-weight: 700;
                    transition-duration: 0.2s;
                }
                li:hover{                    
                    font-size: 4.1vh;
                    color: #E0BE8D;
                }
                .selected{
                    font-size: 14vh;
                    font-weight: 500;
                    font-family: PpeikoThin;
                }
                .selected:hover{                    
                    font-size: 14vh;
                    color: #E0BE8D;
                }
                .punto{
                    min-height: 2vh;
                    font-size: 4vh;
                }
            }
            div{
                min-width: 70%;
                .one{
                    max-width: 75%;
                    border-bottom: 1px solid #E0BE8D;
                    display: flex;
                    flex-direction: column;
                    div{
                        background-color: #E0BE8D;
                        min-height: 2vh;
                        min-width: 5vw;
                        max-width: 5vw;
                    }
                    text{
                        font-size: 3vw;
                        color: #E0BE8D;
                        margin-left: 3vw;
                        padding-top: 2vh;
                        padding-bottom: 2vh;
                        font-family: PpeikoThin;
                        text-transform: uppercase;
                    }
                }
                .two{
                    display: flex;
                    text{
                        margin-top: 3vh;
                        font-size: 1.2vw;
                        margin-left: 3vw;
                        max-width: 11vw;
                        line-height: 3.5vh;
                        font-family: ApexBook;
                        color: #707070;
                    }
                }

            }

            .textDentroDos{
                .one{
                    div{
                        max-width: 10vw;
                    }
                }
            }
            .textDentroTres{
                .one{
                    div{
                        max-width: 15vw;
                    }
                }
            }
            .textDentroCuatro{
                .one{
                    div{
                        max-width: 20vw;
                    }
                }
            }
        }
    }
    .sectionThreeAboutPageThree{
        position: absolute;
        z-index: 3;
        min-width: 100vw;
        min-height: 40vh;
    }
}