@media (max-width: 1200px) and (orientation:landscape){
    .containerTerritorios{
        min-height: 100vh;
        min-width: 100vw;
        display: flex;
        flex-direction: row;
    
        .select{
            display: none;
        }
    
        section{
            min-width: 50vw;
    
    
    
            .fondo0, .fondo1,.fondo2,.fondo3,.fondo4,.fondo5,.fondo6,.fondo7,.fondo8,.fondo9,.fondo10,.fondo11,.fondo12{
                margin-top: 10vh;
                margin-left: 10vw;
                min-width: 40vw;
                max-height: 80vh;           
                
                background-size: cover;
            }
    
            .fondo0{    background-image: url('../../img/territories/img1.png');    }
            .fondo1{    background-image: url('../../img/territories/img2.png');    }
            .fondo2{    background-image: url('../../img/territories/img3.png');    }
            .fondo3{    background-image: url('../../img/territories/img4.png');    }
            .fondo4{    background-image: url('../../img/territories/img5.png');    }
            .fondo5{    background-image: url('../../img/territories/img6.png');    }
            .fondo6{    background-image: url('../../img/territories/img7.png');    }
            .fondo7{    background-image: url('../../img/territories/img8.png');    }
            .fondo8{    background-image: url('../../img/territories/img9.png');    }
            .fondo9{    background-image: url('../../img/territories/img10.png');    }
            .fondo10{    background-image: url('../../img/territories/img11.png');    }
            .fondo11{    background-image: url('../../img/territories/img12.png');    }
            .fondo12{    background-image: url('../../img/territories/img13.png');    }
    
                display: flex;
    
    
        
            section{
                min-width: 15vw;
    
               
              
                
            }
            ul{
                z-index: 2;
                margin-top: 10vh;
                display: flex;
                justify-content: center;
                align-content: center;
                flex-direction: column;
                min-width: 35vw;
                text-decoration: none;
                list-style: none;
                li{
                    text{
                        color: #E0BE8D;
                        font-size: 2vw;
                        font-family: PpeikoLight;
                    }
                    ul{
                        list-style-type:disc;
                        max-width: 20vw;
                        font-size: 1vw;
                        margin-top: 3vh;
                        margin-bottom: 2vh;
                        li{
                            font-size: 1.8vw;
                            transition-duration: 0.5s;
                            font-family: ApexBook;
                            line-height: 3vh;                       
                            list-style-image: url("../../img/territories/liststyle.png");
                            cursor: pointer;                 
                        }
                        li:hover{
                            color: #E0BE8D;
                            font-size: 2vw;
                        }
                    }
                }
                .seleccionado{
                    color: #E0BE8D;                
                }
                .Territories{
                    cursor: pointer;
                    font-size: 3vw;
                    font-family: PpeikoThin;
                    margin-bottom: 3vh;
                }
            }
    
    
    
            .titles{
                display: flex;
                justify-content: center;
                align-items: center;
                border: 1px solid transparent;
                
               text{
                   font-size: 5vw;
                   font-family: PpeikoMedium;         
                   margin-right: 8vw;
                   margin-bottom: 0vw;
                   min-width: 10vw;
                   min-height: 80%;
                   text-align: center;
                   line-height: 14vw;
                   background-color: white;
                   writing-mode: vertical-lr;
                   transform: rotate(180deg);
                   background: linear-gradient(90deg,  transparent 20%,white 50%);
               }
    
    
               .cero{
                    font-size: 10vw;
                    margin-right: 8vw;
                    margin-bottom: -0vw;
               }
               .uno{
                    font-size: 8.5vw;
                    margin-bottom: -0vw;
                }
                .dos{
                font-size: 9.5vw;
                margin-right: 7vw;
                margin-bottom: -0vw;
                }
                .tres{
                    font-size: 10vw;
                    margin-right: 7vw;
                    margin-bottom: -0vw;
                }
                .cuatro{
                    font-size: 9vw;
                    margin-right: 7vw;
                    margin-bottom: -0vw;
                }
                .cinco{
                    font-size: 8.5vw;
                    margin-right: 7vw;
                    margin-bottom: -0vw;
                }
                .seis{
                    font-size: 8vw;
                    margin-right: 7vw;
                    margin-bottom: -0vw;
                }
                .siete{
                    font-size: 11vw;
                    margin-right: 7vw;
                    margin-bottom: -0vw;
                }
                .ocho{
                    font-size: 8vw;
                    margin-right: 7vw;
                    margin-bottom: -0vw;
                }
                .nueve{
                    font-size: 8vw;
                    margin-right: 7vw;
                    margin-bottom: -0vw;
                }
                .diez{
                    font-size: 12vw;
                    margin-right: 7vw;
                    margin-bottom: 0vw;
                }
                .once{
                    font-size: 7vw;
                    margin-right: 7vw;
                    margin-bottom: 0vw;
                }
                .doce{
                    font-size: 8vw;
                    margin-right: 7vw;
                    margin-bottom: -0vw;
                }
            }
        }
    }   
}