@media (max-width: 1200px){
    .containerContactPageTwo{
        min-width: 100vw;
        min-height: 100vh;
        margin-top: 10vh;
        margin-bottom: 10vh;
        display: flex;
        flex-direction: row;
    
        .sectionOneContactPageTwo{
            min-width: 55vw;
            max-width: 55vw;
            display: flex;
            justify-content: flex-end;
            align-items: center;
            .Form{
                min-width: 90%;
                max-width: 90%;
                display: flex;
                flex-direction: column;
                align-items: flex-end;
                div{
                    
                    margin-top: 1.5rem;
                    margin-right: 5%;
                    min-width: 90%;
                    max-width: 90%;
                    display: flex;
                    flex-direction: column;
                    label{
                        display: flex;
                        min-height: 3rem;
                        font-family: PpeikoLight;
                        font-size: 1.4vw;
                        color: #707070;
                    }
                    input{
                        border: 1px solid #E0BE8D;
                        min-height: 55px;
                    }
                    .message{
                        min-height: 200px;
                    }
                    button{
                        min-width: 20%;
                        max-width: 20%;
                        min-height: 60px;
                        margin-left: 80%;
                        border: 1px solid #C7974B;
                        background-color: #C7974B;
                        font-size: 1.2vw;
                        color: #FFF;
                        font-family: ApexBook;
                    }
                }
            }
        }
        .sectionTwoContactPageTwo{
            min-width : 40vw;
            max-width: 40vw;
            min-height: 100vh;
            background-image: url('../../img/contact/pagetwo/fondo.png');
            background-size: cover;
            background-position: center;
    
            .filtro{
                min-width: 30vw;
                max-width: 30vw;
                margin-left: 3vw;
                min-height: 90vh;
                background-color: transparent;
                display: flex;
                justify-content: center;
                align-items: center;
    
    
                .sello1{
                    position: absolute;
                    min-width: 8vw;
                    min-width: 8vw;
                    margin-bottom: 70vh;
                    margin-left: -36vw;
                    background-color: #C7974B;
                    min-height: 1.5rem;
                    max-height: 1.5rem;
                }
                .sello2{
                    position: absolute;
                    min-width: 8vw;
                    min-width: 8vw;
                    margin-bottom: -40vw;
                    margin-left: 42vw;
                    background-color: #C7974B;
                    min-height: 1.5rem;
                    max-height: 1.5rem;
                }
                .titulo{
                    position: absolute;
                    color: #FFF;
                    margin-bottom: 50vh;
                    font-size: 5.5vw;
                    font-family: PpeikoMedium;
                }    
                .direccion{
                    max-width: 15vw;
                    position: absolute;
                    margin-bottom: 10vh;
                    margin-right: 5VW;
                    color: #FFF;
                    font-size: 1.4vw;
                    font-family: ApexBook;
                }
                .iconos{
                    min-width: 20vw;
                    position: absolute;
                    margin-bottom: -75vh;
                    margin-left: 0vw;
                    max-width: 20vw;
                    display: flex;
                    justify-content: space-between;
                    align-items: center;
                    a{
                        cursor:auto;
                    }
                }
            }
        }
    }
}