@media (max-width: 1200px) {


    .containerGeneralHeader{
        z-index: 100;
        min-width: 100vw;
        max-width: 100vw;
        min-height: 10vh;    
        max-height: 10vh;
        position: fixed;
        margin-top: 0;
        margin-left: 0;
        background-color: #151515;
        border-bottom: 1px solid #C7974B;
        display: flex;
        justify-content: flex-start;
        align-items: center;
        .botonOpenOpen{        
            cursor: pointer;
            min-width: 8vh;
            max-width: 8vh;
            min-height: 8vh;
            max-height: 8vh;
            margin-left: 5vw;
            display: flex;
            justify-content: center;
            align-items: center;
            display: none;
            hr{
                position: absolute;
                border: 1px solid #DDB982;
                min-width: 6vh; 
                transition-duration: 0.5s;
            }
            .lineOne{   
                margin-bottom: 1vh;
            }
            .lineTwo{
                margin-top: 1vh;
            }        
        }
        .botonOpenOpen:hover{
            display: none;
            .lineOne{   
                margin-right: 2vh;
            }
            .lineTwo{
                margin-left: 2vh;
            }
        }
    
    
    
        .botonClose{
            z-index: 120;
            cursor: pointer;
            min-width: 8vh;
            max-width: 8vh;
            min-height: 8vh;
            max-height: 8vh;
            margin-left: 5vw;
            display: flex;
            justify-content: center;
            align-items: center;
            display: none;
            hr{
                position: absolute;
                border: 1px solid #DDB982;
                min-width: 6vh; 
                transition-duration: 0.5s;
            }
            .lineOne{   
                margin-bottom: 0vh;
                max-width: 5vw;
                transform: rotate(45deg);
            }
            .lineTwo{
                margin-top: 0vh;
                transform: rotate(-45deg);
            }        
        }
        .botonClose:hover{
            .lineOne{   
            }
            .lineTwo{
                min-width: 5.05vw;
            }
        }
    
    
    
        .imgOne{
           
            z-index: 110;
            position: absolute;
            min-width: 50vw;
            min-height: 100vh;
            margin-top: 90vh;
            animation: animationImgOne 1s;
            background-image: url('../../img/header/fondo1.png');
            background-size: 100%;
            background-color: rgba(0,0,0, 0.5);
            background-repeat: no-repeat;
            display: none;
        }
        .imgDos{
            z-index: 90;
            position: absolute;
            min-width: 100vw;
            min-height: 100vh;
            //margin-top: 60vh;
            margin-top: 90vh;
    
            animation: animationImgTwo 1s;
            display: flex;
            justify-content: flex-end;
            background-image: url('../../img/header/fondo2.png');
            background-size: 100%;
            background-repeat: no-repeat;
            align-items: flex-start;
            display: none;
            ul{
                margin-top: 10vh;
                min-width: 40vw;
                margin-left: 10%;
                display: flex;
                flex-direction: column;
                justify-content: flex-start;
                .link{
                    display: flex;
                    text-decoration: none;
                    list-style: none;
                    color: #E0BE8D;
                    font-size: 2vw;
                    line-height: 8vh;
                    font-family: PublicoHeadlineLightItalic;
                  
                    text{
                        background-repeat: no-repeat;
                        background-image:   linear-gradient(to right, #E0BE8D 100%, #C9C9C9 100%);
                        background-size:   20px 2px;//,
                        background-position:   0% 75%;   
                    }
                       
                }
                .link:hover{
                    text{
                        transition-duration: 0.5s;
                        background-size:    100% 2px;//,  
                    }
                    
                }
            }
        }
        .imgTres{
            z-index: 110;
            position: absolute;
            min-width: 50vw;
            min-height: 30vh;
            margin-top: 160vh;
            margin-left: 50vw;
            animation: animationImgThree 1s;
            background-image: url('../../img/header/fondo3.png');
            background-size: 100%;
            background-repeat: no-repeat;
            display: none;
        }
    
    
        @keyframes animationImgOne {
            0%{
                margin-left: -30vw;
            }    
            100%{
                margin-left: 0vw;
            }
        }
        @keyframes animationImgTwo {
            0%{
                margin-top: 60vh;
            }
            100%{
                margin-top: 90vh;
            }
        }
        @keyframes animationImgThree {
            0%{
                margin-top: 190vh;
            }
            100%{
                margin-top: 160vh;
            }
        }
    
    
    
    
    
    
        .menuDesktop{
            position: absolute;            
            min-width: 70vw;
            margin-left: 15vw;
            display: flex;
            justify-content: space-between;
            align-items: center;
        
            .link{
                text-decoration: none;
                list-style: none;
                margin-left: 1vw;
                margin-right: 1vw;
                color: #E0BE8D;
                font-size: 1.8vw;
                font-family: PublicoHeadlineLightItalic;
                line-height: 2.8vh;
    
                background-repeat: no-repeat;
                background-image:   linear-gradient(to right, #E0BE8D 100%, #C9C9C9 100%);
                background-size:   20px 2px;//,
                background-position:   0% 98%;   
                text-transform: uppercase;               
            }
            .link:hover{   
                transition-duration: 0.5s;
                background-size:    100% 2px;//,      
                text-transform: uppercase;                    
            }
    
           
        }
    
        .LogoDesktop{
            position: absolute;
            margin-left: 5vw;
            min-width: none;
            min-width: 2vw;
        }
    
    
    }


}