@media (max-width: 1180px) and (orientation:landscape){
    .containerAboutPageTwo{
        min-width: 100vw;
        min-height: 100vh;
        max-width: 100vw;
        max-height: 100vh;
        margin-top: 10vh;
        margin-bottom: 10vh;
        display: flex;
        align-items: flex-end;
        flex-direction: row-reverse;
        .sectionOneAboutPageTwo{
            z-index: 3;
            min-width: 50vw;
            min-height: 100vh;
            .sectionContentOneAboutPageTwo{
                min-height: 60vh;
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                .one{
                    position: absolute;
                    min-width: 8vw;
                    margin-bottom: 45vh;
                    margin-right: 50vw;
                    border: 1px solid  #707070;
                }
                .two{
                    position: absolute;
                    min-width: 3vw;
                    max-width: 3vw;
                    margin-right: 148vw;
                    margin-top: 40vh;
                    border: 1px solid  #707070;
                }
                h2{
                    z-index: 5;
                    position: absolute;
                    font-family: PpeikoLight;
                    font-size: 8vw;
                    
                    font-weight: 400;
                    color: #E0BE8D;
                    min-width: 30vw;
                    margin-top: 40vh;
                    margin-right: 15vw;
                    text-align: right;
                }
                h1{
                    min-width: 30vw;
                    z-index: 4;
                    position: absolute;
                    font-family: PpeikoMedium;
                    font-size: 18vw;
                    font-weight: 400;
                    margin-right: 16vw;
    
                }
                .sello1{
                    position: absolute;
                    min-width: 8vw;
                    min-height: 1.5rem;                
                    background: rgb(224,190,141);
                    background: linear-gradient(90deg, rgba(224,190,141,1) 0%, rgba(199,151,75,1) 100%);               
                    margin-bottom: 10vh;
                    margin-right: 140vw;
                    z-index: 20;
                }
                .sello2{
                    position: absolute;
                    min-width: 8vw;
                    margin-right: 50vw;
                    margin-top: 100vh;
                    min-height: 1.5rem; 
                    background: rgb(224,190,141);
                    background: linear-gradient(90deg, rgba(224,190,141,1) 0%, rgba(199,151,75,1) 100%);               
                }
            }
        }
        .sectionTwoAboutPageTwo{
            z-index: 2;
            min-width: 45vw;
            min-height: 100vh;
            background-image: url('../../img/about/pagetwo/fondo.png');
            background-size: cover;
            background-repeat: no-repeat;
            display: flex;
            justify-content: flex-start;
            align-items: flex-end;
            text{
                position: absolute;
                background-color: #FFF;
                padding: 0vw;
                margin-left: -3vw;
                margin-bottom: 25vh;
                min-width: 30vw;
                max-width: 30vw;
                font-size: 1.2vw;
                color: #707070;
                display: flex;
                justify-content: center;
                align-items: center;
                line-height: 2.4vh;
                flex-direction: column;
                font-family: ApexBook;
                text-align: justify;
                label{
                    min-width: 100%;
                    color: #C7974B;
                    font-size: 1.4vw;
                    text-align: left;
                    font-family: PpeikoLight;
                }
            }
        }
        .sectionThreeAboutPageTwo{
            position: absolute;
            z-index: 3;
            min-width: 100vw;
            min-height: 40vh;
        }
    }
}