@media (max-width: 1180px) and (orientation:landscape){




    .containerContentPageFive{
        background-color: transparent;
        min-width: 100vw;
        min-width: 100vw;
        min-height: 35vh;
        display: flex;
        flex-direction: column;
        align-items: center;
    
