        $animationSpeedFive: 30s;
        
        // Animation
        @keyframes scrollFive {
            0% { transform: translateX(0); }
            100% { transform: translateX(calc(-50vw * 9))}
        }
        
        
        // Styling
        .sliderFive {
            background-color: none;
            min-width: 100vw;
            max-width: 100vw;
            
            min-height: 40vh;
            max-height: 40vh;
            overflow:hidden;
            overflow-x: hidden;
            display: flex;
            justify-content: flex-start;
            align-items: flex-end;
            &::before,
            &::after {
                content: "";
                height: 100%;
                width: 10px;
                z-index: 2;
            }
            
            &::after {
                right: 0;
                top: 0;
                transform: rotateZ(180deg);
            }
        
            &::before {
                left: 0;
                top: 0;
            }
            
            .slide-trackFive {
                animation: scrollFive $animationSpeedFive linear infinite;
                display: flex;
                width: calc(50vw * 18);
            }
            .slide-trackFive:hover {
                //animation-play-state: paused;
            }
            
            .slideFive {
                min-height: 45vh;
