@media (max-width: 1180px) and (orientation:portrait){

        .containerAboutPageThree{
            margin-top: 20vh;
            min-width: 100vw;
            min-height: 90vh;
            max-width: 140vw;
            display: flex;
            align-items: flex-start;
            justify-content: flex-start;
            flex-direction: column;
            margin-bottom: 25vh;
    
            .sectionOneAboutPageThree{
                z-index: 3;
                min-width: 100vw;
                min-height: 40vh;
                .sectionContentOneAboutPageThree{
                    min-height: 40vh;
                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                    align-items: center;
                    .one{
                        display: none;
                        position: absolute;
                        min-width: 8vw;
                        margin-bottom: 25vh;
                        margin-right: 50vw;
                        border: 1px solid  #707070;
                    }
                    .two{
                        position: absolute;
                        min-width: 3vw;
                        max-width: 3vw;
                        margin-right: 148vw;
                        margin-top: 40vh;
                        border: 1px solid  #707070;
                    }
                    h2{
                        z-index: 5;
                        position: absolute;
                        font-family: PpeikoLight;
                        font-size: 8vw;                
                        font-weight: 400;
                        color: #E0BE8D;
                        min-width: 30vw;
                        margin-top: 20vh;
                        margin-right: -45vw;
                    }
                    h1{
                        min-width: 30vw;
                        z-index: 4;
                        position: absolute;
                        font-family: PpeikoMedium;
                        font-size: 18vw;
                        font-weight: 400;
                        margin-left: 0vw;
                        margin-top: 30vh;
                    }
                    .sello1{
                        display: none;
                    }
                    .sello2{
                        position: absolute;
                        min-width: 70vw;
                        max-width: 70vw;
                        margin-right: 10vw;
                        margin-top: 50vh;
                        min-height: 1.5rem; 
                        font-size: 3vw;
                        color: #E0BE8D;            
                        font-family: PpeikoLight;
                    }
                }
            }
            .sectionTwoAboutPageThree{
                z-index: 5;
                margin-left: 0;
                min-width: 100vw;
                max-width: 100vw;
                min-height: 40vh;
                display: flex;
                justify-content: flex-start;
                align-items: flex-start;
                .text{
                    margin-top: 10vh;
                    border-top: 1px solid #E0BE8D;
                    min-width: 100vw;
                    display: flex;
                    flex-direction: row;
                    ul{
                        min-width: 20vw;                            
                        max-width: 20vw;
                        text-decoration: none;
                        list-style: none;
                        border-right: 1px solid #E0BE8D;
                        font-family: PpeikoThin;
                        li{
                            min-height: 8vh;
                            display: flex;
                            align-items: center;
                            justify-content: center;
                            font-size: 4vh;
                            min-width: 80%;
                            font-weight: 500;
                        }
                        .selected{
                            font-size: 14vh;
                            font-weight: 500;
                            font-family: PpeikoThin;
                            animation: ApareceNumero 0.5s;
                        }
                        @keyframes ApareceNumero {
                            0%{
                                font-size: 4vh;
                            }
                            100%{
                                font-size: 14vh;
                            }
                        }
                        .punto{
                            min-height: 2vh;
                            font-size: 4vh;
                        }
                    }
                    div{
                        min-width: 70%;
                        .one{
                            max-width: 100%;
                            border-bottom: 1px solid #E0BE8D;
                            display: flex;
                            flex-direction: column;
                            div{
                                background-color: #E0BE8D;
                                min-height: 2vh;
                                min-width: 10vw;
                                max-width: 10vw;
                            }
                            text{
                                font-size: 8vw;
                                color: #E0BE8D;
                                margin-left: 5vw;
                                padding-top: 2vh;
                                padding-bottom: 2vh;
                                font-family: PpeikoThin;
                                text-transform: uppercase;
                            }
                        }
                        .two{
                            display: flex;
                            text{
                                margin-top: 3vh;
                                font-size: 4vw;
                                margin-left: 8vw;
                                max-width: 50vw;
                                line-height: 4vh;
                                font-family: ApexBook;
                                color: #707070;
                            }
                        }
        
                    }
        
                    .textDentroDos{
                        .one{
                            div{
                                max-width: 25vw;
                            }
                        }
                    }
                    .textDentroTres{
                        .one{
                            div{
                                max-width: 40vw;
                            }
                        }
                    }
                    .textDentroCuatro{
                        .one{
                            div{
                                max-width: 65vw;
                            }
                        }
                    }
                }
            }
            .sectionThreeAboutPageThree{
                display: none;
                position: absolute;
                z-index: 3;
                min-width: 100vw;
            }
        }
    
    
}